body{
    width: 100vw;
    overflow-x: hidden;
    background-color: white;
    color: black;
    flex-direction: column;
}

.App{
    width: 100vw;
}

.sombra{
    box-shadow: none !important;
    transition: box-shadow 0.3s;
}

.sombra:hover{
    box-shadow: #e6ebfa 0px 0px 20px 10px  !important;
    cursor: pointer;
}