hr{
    border-bottom: 3px solid #27374f;
    width: 60%;
    margin-top: 50px;
    margin-bottom: 50px;
}

.cont-nosotros{
    width: 100vw;
    margin-top: 100px;
    color: #27374f;
}

.cont-T-nosotros{
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
}


.T-nosotros{
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-bottom: 50px;
}

.T-nosotros:nth-child(2)>div>div:nth-child(2){

}

.T-nosotros>div{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-bottom: 50px;
}

.T-nosotros>div>div{
    width: 35%;
    text-align: justify;
    min-height: 350px;
    height: auto;
    color: #27374f;
}

.T-nosotros>div>div>h3{
    font-size: 25px;
    text-align: center;
}

.T-nosotros>div>div>p{
    line-height: 1.6;
}

.T-nosotros:nth-child(1)>div>div:nth-child(2),
.T-nosotros:nth-child(2)>div>div:nth-child(2),
.T-nosotros:nth-child(3)>div>div:nth-child(2){
    background-position: top center;
    background-size: 100% 100% !important;
    box-shadow: inset 0px 0px 4px 4px rgba(0, 0, 0, 0.363);
    border-radius: 25px;
    transition: background 0.4s;
}

.T-nosotros:nth-child(1)>div>div:nth-child(2):hover,
.T-nosotros:nth-child(2)>div>div:nth-child(2):hover,
.T-nosotros:nth-child(3)>div>div:nth-child(2):hover{
    cursor: pointer;
    background-size: 105% 105% !important;
}

.T-nosotros:nth-child(1)>div>div:nth-child(2){
    background-image: url("../../images/imgInicio14.jpg");
}

.T-nosotros:nth-child(2)>div>div:nth-child(2){
    background-image: url("../../images/niña_pequeña.png");
}


.T-nosotros:nth-child(3)>div>div:nth-child(2){
    background-image: url("../../images/imgInicio14.jpg");
}

.left>div{
    flex-direction: row;
}

.right>div{
    flex-direction: row-reverse;
}

.info-Instituto{
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 50px;
}

.info-Instituto>h3{
    text-align: center;
}

.info-Instituto>p{
    text-align: justify;
    width: 80%;
    line-height: 1.4;
}

.cont-vmm{
    width: 80%;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}


.cont-vmm>div{
    width: 300px;
    margin: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: box-shadow 0.4s;
}

.cont-vmm>div:hover{
    box-shadow: #e6ebfa 0px 0px 20px 10px;
    cursor: pointer;
}

.cont-vmm>div svg{
    font-size: 70px;
    margin-top: 10px;
}

.cont-vmm>div>h3{
    font-size: 30px;
}

.cont-vmm>div>p{
    text-align: justify;
    line-height: 1.5;
    padding: 0px 20px;
    font-size: 14px;
}

.cont-principios{
    width: 100vw;
    height: 850px;
    background-color: #ECF0F1BF;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 50px;
}

.cont-principios>div:first-child{
    margin-top: 50px;
}


.cont-principios>div:first-child>h2{
    text-align: center;
    font-size: 20px;
    font-weight: 100;
    margin-top: 0px;
    margin-bottom: 5px;
}

.principios{
    width: 500px;
    height: 500px;
    display: grid;
    grid-template-columns: repeat(2,50%);
    grid-template-rows: repeat(2,50%);
    /* grid-gap: 5%; */
    align-items: center;
    justify-items: center;
    transform: rotateZ(45deg) translate(100px,100px);
}


.principios>div{
    width: 92%;
    height: 92%;
    background-color: grey;
    position: relative;
    overflow: hidden;
    box-shadow: #e6ebfa 0px 0px 6px 6px;
}


.principios>div>div{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotateZ(-45deg);
}

.principios>div:nth-child(1){
    background-color: hsla(337, 100%, 46%, 0.4);
}


.principios>div:nth-child(1)::before,
.principios>div:nth-child(2)::before,
.principios>div:nth-child(3)::before,
.principios>div:nth-child(4)::before{
    content: "";
    position: absolute;
    width: 140%;
    height: 140%;
    top: -20%;
    left: -20%;
    background-position: center;
    background-size:100% 100%;
    background-repeat: no-repeat;
    z-index: -1;
    transform: rotateZ(-45deg);
    transition: background 0.1s;
}

.principios>div:nth-child(1):hover::before,
.principios>div:nth-child(2):hover::before,
.principios>div:nth-child(3):hover::before,
.principios>div:nth-child(4):hover::before{
    background-size:110% 110%;
    transition: background 0.7s;
}

.principios>div:nth-child(1)>div:nth-child(2),
.principios>div:nth-child(2)>div:nth-child(2),
.principios>div:nth-child(3)>div:nth-child(2),
.principios>div:nth-child(4)>div:nth-child(2){
    display: flex;
    font-size: 25px;
    justify-content: center;
    align-items: center;
    transition: color 0.3s;
    color: transparent;
}

.principios>div:nth-child(1)>div:nth-child(1),
.principios>div:nth-child(2)>div:nth-child(1),
.principios>div:nth-child(3)>div:nth-child(1),
.principios>div:nth-child(4)>div:nth-child(1){
    transition: color 0.6s;
    color: #27374f;
}


.principios>div:nth-child(1):hover>div:nth-child(2),
.principios>div:nth-child(2):hover>div:nth-child(2),
.principios>div:nth-child(3):hover>div:nth-child(2),
.principios>div:nth-child(4):hover>div:nth-child(2){
    color: #27374f;
    transition: color 0.6s;
}


.principios>div:nth-child(1)>div:nth-child(2) p,
.principios>div:nth-child(2)>div:nth-child(2) p,
.principios>div:nth-child(3)>div:nth-child(2) p,
.principios>div:nth-child(4)>div:nth-child(2) p{
    padding: 0px 55px;
    text-align: center;
}

.principios>div:nth-child(1):hover>div:nth-child(1),
.principios>div:nth-child(2):hover>div:nth-child(1),
.principios>div:nth-child(3):hover>div:nth-child(1),
.principios>div:nth-child(4):hover>div:nth-child(1){
    
    transition: color 0.3s;
    color: transparent;
}

.principios>div>div{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 35px;
    width: 140%;
    height: 140%;
    overflow: hidden;
}


.principios svg{
    font-size: 70px;
    font-weight: 900;
}


.principios>div:nth-child(1)::before{
    background-image: url("../../images/imgInicio1.jpeg");
}

.principios>div:nth-child(2)::before{
    background-image: url("../../images/imgInicio2.png");
}
.principios>div:nth-child(3)::before{
    background-image: url("../../images/imgInicio3.png");
}
.principios>div:nth-child(4)::before{
    background-image: url("../../images/imgInicio4.png");
}

.principios>div:nth-child(2){
    background-color: hsla(48, 89%, 70%,0.4);
}
.principios>div:nth-child(3){
    background-color: hsla(205, 100%, 46%,0.4);
}
.principios>div:nth-child(4){
    background-color: hsla(283, 39%, 58%,0.4);
}

.cont-himno{
    border-left: 5px solid #900C3F;
    border-right: 5px solid #900C3F;
    padding: 10px 200px;
}

.cont-himno>p,
.cont-himno>h2{
    text-align: center;
}

.cont-himno>p{
    font-style: italic;
}

.cont-himno>h2{
    margin-bottom: 50px;
}

@media (max-width:820px) {
    .cont-principios{
        height: 600px;
    }

    .principios{
        width: 300px;
        height: 300px;
    }
    
    .principios h4,
    .principios svg{
        font-size: 20px;
    }
    
    .principios p{
        font-size: 14px;
        font-weight: 600;
    }

    
    .T-nosotros>div>div{
        width: 40%;
    }

    .T-nosotros>div>div{
        min-height: 200px;
    }

    .T-nosotros>div>div>p{
        line-height: 1.2;
        font-size: 15px;
    }
}

@media (max-width:580px){
    .T-nosotros>div>div h3{
        font-size: 20px;
    }
    .T-nosotros>div>div p{
        font-size: 12px;
    }
}

@media (max-width:450px){
    .cont-principios{
        height: 400px;
    }

    .principios{
        width: 200px;
        height: 200px;
    }

    .principios h4,
    .principios svg{
        font-size: 15px;
    }
    
    .principios p{
        font-size: 8px;
        font-weight: 600;
    }

    .principios{
        transform: rotateZ(45deg) translate(50px,50px);
    }
    
}

@media (max-width:360px) {

    .T-nosotros>div>div:nth-child(1){
        width: 80%;
    }

    .T-nosotros>div>div:nth-child(2){
        display: none;
    }
}