.cont-constancias{
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.cont-constancias>div:nth-child(2){
    margin: 100px;
    box-shadow: #e6ebfa 0px 0px 20px 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.cont-constancias>div>h2{
    margin-top: 50px;
}

.cont-constancias>div>div{
    margin-top: 50px;
    width: 80%;
}

.cont-constancias>div>div:last-child{
    text-align: center;
}

.cont-constancias>div>div:last-child svg{
    font-size: 40px;
    margin-top: 20px;
}

.cont-constancias>div>div:last-child p{
    margin: 0;
}

.cont-constancias>div>div:last-child>div{
    margin-top: 40px;
}

.cont-constancias>div>div:last-child>div button{
    background-color: #27374f;
    color: white;
    margin-bottom: 100px;
    margin-top: 20px;
}

.cont-constancias>div>div:last-child>div button:hover{
    cursor: pointer;
}

ul{
    padding-left: 20px;
}

ol{
    padding-left: 0;
    line-height: 1.3;
}

ol::before{
    content: "•";
    position: relative;
    left: -10px;
    font-size: 20px;
}
.cont-constancias a{
    color: #27374f !important;
}