.footer{
    position: relative;
    width: 100vw;
    min-height: 500px;
    background-color: #27374f;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer>div{
    width: 90%;
}


.footer>div:first-child{
    border-bottom: 1px solid white;
    display: flex;
    align-items: center;
}

.footer>div:first-child>img{
    width: 80px;
    height: 90px;
}

.footer>div:first-child>h2{
    font-size: 50px;
    font-weight: 100;
    margin: 30px 10px;
}


.footer>div:last-child{
    margin-top: 40px;
    display: flex;
    justify-content: space-between;
}

.footer>div:last-child>div{
    width: 33%;
}

.footer>div:last-child>div>div>h3{
    font-weight: 100;
    font-size: 27px;
    margin-bottom: 5px;
}


.footer>div:last-child>div>div>div{
    width: 100px;
    height: 1px;
    background-color: white;
}


.footer>div:last-child>div>p{
    margin: 20px 0px;
}


.footer>div:last-child>div:nth-child(2)>div>p{
    margin-left: 20px;
}

p{
    word-wrap: break-word !important;
}

@media (max-width:550px) {
    .footer>div:first-child>h2{
        font-size: 35px;
    }

  
    .footer>div:last-child>div>div>h3{
        font-size: 20px;
    }

    .footer>div:last-child>div>p{
        font-size: 12px;
    }
    .footer>div:last-child>div>div>p{
        font-size: 12px;
    }
}

@media (max-width:380px) {
    .footer>div:first-child>h2{
        font-size: 25px;
    }

    .footer>div:last-child>div>div>h3{
        font-size: 15px;
    }

    .footer>div:last-child>div>p{
        font-size: 10px;
    }
    .footer>div:last-child>div>div>p{
        font-size: 10px;
    }

}