.cont-excusas{
    width: 100vw;
    margin-top: 200px;
    margin-bottom: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #27374f;
}

.cont-excusas>div{
    width: 300px;
    height: 200px;
    box-shadow: #e6ebfa 0px 0px 20px 10px;
    padding: 30px 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.cont-excusas>div>a>svg{
    color: #27374f;
    font-size: 70px;
    transition: transform 0.3s;
}

.cont-excusas>div>a>svg:hover{
    transform: translatey(-5px);
}