.BarraNav{
    position: fixed;
    width: 100vw;
    top: 0;
    left: 0;
    height: 100px;
    z-index: 1000;
    background-color: #27374f;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: white;
}

.BarraNav .BLogo{
    /* width: 30%; */
    margin-left: 10%;
}

.BarraNav .BLogo img{
    width: 90px;
    height: 100px;
}

.BarraNav .BMenu{
    /* width: 50%; */
    display: flex;
    justify-content: start;
    margin-right: 15%;
}
.BMenu>div{

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100px;
}
.BMenu>div:nth-child(4){

    position: relative;
}
.BMenu>div:nth-child(4):hover{

    cursor: context-menu;
}
.BMenu>div:nth-child(4)>div{

    display: none;
    position: absolute;
    top: 100%;
    flex-direction: column;
    background-color: white;
    width: 350px;
    border-bottom-left-radius: 7px;
    border-bottom-right-radius: 7px;
    border-bottom: 7px solid #900C3F;
    box-shadow: 0px 0px 2px 1px;
}
.BMenu>div:nth-child(4)>div>a{
    font-size: 14px;
    color: #27374f;
}
.BMenu>div:nth-child(4)>div>a:hover{
    border: 1px solid #27374f;
    color: #900C3F;

}
.BMenu>div:nth-child(4):hover>div{

    display: flex;
}

.BarraNav a{
    text-decoration: none;
    color: white;
    font-weight: 700;
    padding: 15px 30px;
}

.BarraNav a:hover{
    color: #064CFF;
}

.active{
    border: 1px solid #064CFF;
}

.cont-menuLat{
    display: none;
    margin-right: 50px;
    padding: 12px;
    transition: background 0.3s;
    border-radius: 50%;
}
.cont-menuLat:hover{
    background-color: rgba(128, 128, 128, 0.726);
    cursor: pointer;
}

.cont-menuLat>svg{
    font-size: 30px;
}

.bnt-contacto{
    display: none;
    background-color: white;
    height: 40px;
    width: 150px;
    border-radius: 20px;
}

.bnt-contacto>a{
    width: 100%;
    height: 100%;
    color: #27374f;
    padding: 0%;
    display: flex;
    align-items: center;
    justify-content: center;
}


@media (max-width:920px) {
    .bnt-contacto{
        display: block;
    }
    .BarraNav>.BLogo{
        margin-left: 0;
    }

    .BarraNav>.BLogo img{
        width: 50px;
        height: 60px;
    }

    .cont-menuLat{
        display: block;
    }

    .BarraNav>.BMenu{
        display: none;
        margin: 0;
        flex-direction: column;
        position: absolute;
        top: 0;
        right: 0;
        height: 100vh;
        background-color: #27374fec;
    }
    .visible{
        display: flex !important;
    }
    .BarraNav>.BMenu>div{

        margin: 30px 40px;
    }
    .BarraNav>.BMenu>div>a{
    
        width: 150px !important;
    }

    .visible ~ .cont-menuLat{
        margin-right: 300px ;
    }

    .BMenu>div:nth-child(4)>div{
        width: 250px;
        max-height: 300px;
        overflow-y: scroll;
        bottom: 100%;
        top: auto;
    }
}

@media (max-width:670px){
    .bnt-contacto{
        width: 100px;
        font-size: 12px;
    }
    .BarraNav>.BMenu>div>a{
        width: 100px !important;
    }
    .BMenu>div:nth-child(4)>div{
        width: 170px;
    }
    .visible ~ .cont-menuLat{
        margin-right: 250px !important;
    }
}


@media (max-width:520px){
    .BarraNav>.BMenu>div{
        margin: 30px 20px;
    }
    .BarraNav>.BMenu>div>a{
        width: 100px !important;
        padding: 15px 20px !important;
    }
    .BMenu>div:nth-child(4)>div{
        width: 170px;
    }

    .visible ~ .cont-menuLat{
        margin-right: 180px !important;
    }
}


@media (max-width:400px){
    .BarraNav>.BMenu>div{
        margin: 30px 10px;
    }
    .BarraNav>.BMenu>div>a{
        width: 100px !important;
        padding: 15px 10px !important;
    }
    .BMenu>div:nth-child(4)>div{
        width: 120px;
    }

    .visible ~ .cont-menuLat{
        margin-right: 180px !important;
    }
}