.cont-inicio{
    width: 100vw;
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #27374f;
}

.cont-quienes{
    margin-top: 50px;
    margin-bottom: 30px;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.cont-quienes>div:first-child{
    margin: 5%;
    width: 25%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.cont-quienes>div:first-child>h3{
    width: 100%;
    font-family: 'Times New Roman', Times, serif;
    font-size: 30px;
    border: 1px solid black;
    padding: 10px 10px;
    text-align: left;
}

.cont-quienes>div:first-child>p{
    text-align: justify;
    color: rgb(99, 99, 99);
    line-height: 1.5;
}


.cont-quienes>div:first-child>button{
    background-color: #27374f;
    color: white;
    border-radius: 30px;
    padding: 10px 30px;
}


.cont-quienes>div:last-child{
    margin: 5%;
    min-width: 50%;
    display: grid;
    grid-template-columns: repeat(2,315px);
    grid-template-rows: repeat(3,206px);
    grid-gap: 10px;
}

.cont-quienes>div:last-child>div{
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    border-radius: 25px;
    box-shadow: inset 0px 0px 3px 3px rgba(0, 0, 0, 0.575);
}

.cont-quienes>div:last-child>div{
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 100% 100% !important;
    /* background-size: cover; */
    transition: background 0.7s;
}

.cont-quienes>div:last-child>div:nth-child(1){
    background-image: url("../../images/imgInicio10.png");
}

.cont-quienes>div:last-child>div:nth-child(2){
    background: url("../../images/imgInicio11.png");
}
.cont-quienes>div:last-child>div:nth-child(3){
    background: url("../../images/imgInicio12.png");
    /* background-size: 100% 210% !important; */
}
.cont-quienes>div:last-child>div:nth-child(4){
    background: url("../../images/imgInicio13.png");
}

.cont-quienes>div:last-child>div:hover{
    cursor: pointer;
    background-size: 105% 105% !important;
}

.cont-quienes>div:last-child>div:nth-child(3)>img:hover{
    width: calc(100% + 100px);
    height: calc(100% + 220px);

}


.cont-quienes>div:last-child>div:nth-child(1){
    grid-row: 1/3;
}

.cont-quienes>div:last-child>div:nth-child(4){
    grid-row: 2/-1;
}

.cont-inicio hr{
    width: 60%;
    border: 1px #27374f solid;
    margin-top: 70px;
    margin-bottom: 130px;
}


.cont-NServicios{
    width: 100vw;
    min-height: 500px;
    background-color: #F5F5F5;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}

.cont-NServicios>div:first-child{
    display: flex;
    flex-direction: column;
}

.cont-NServicios>div:first-child>h2{
    text-align: center;
    font-weight: 400;
}

.cont-NServicios>div:first-child>p{
    margin: 0;
    color: rgb(99, 99, 99);
}


.cont-tarj-servicios{
    margin-top: 50px;
    /* width: 80%; */
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}


.cont-tarj-servicios>div{
    width: 270px;
    min-height: 330px;
    height: auto;
    margin: 20px 30px;
    background-color: inherit;
    border-radius: 10px;
    display: flex;
    flex-direction:column;
    align-items:center;
    justify-content:start;
    transition: box-shadow 0.2s,background 0.2s;
}

.cont-tarj-servicios>div:hover{
    cursor: pointer;
    background-color: white;
    box-shadow: #e6ebfa 0px 0px 20px 10px;
}
.log-services{
    margin-top: 20px;
}

.log-services>svg{
    font-size: 80px;
    color: #27374f;
}

.cont-tarj-servicios>div>h2{
    text-align: center;
    font-size: 22px;
    font-weight: 100;
    color: #27374f;
}

.cont-tarj-servicios>div>p{
    margin: 15px 30px;
    text-align: center;
    font-size: 14px;
    line-height: 1.4;
    color: #27374f;
}

.cont-base-service{
    width: 60%;
    height: 70px;
    background-color: white;
    box-shadow: rgba(128, 128, 128, 0.377) 0px 4px 1px 3px;
    margin-top: 50px;
    margin-bottom: 50px;
    border-radius: 50%;
}

.cont-Niveles{
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.cont-Niveles>h2{
    font-size: 25px;
    margin: 0%;
    margin-bottom: 50px;
}

.cont-NAcademicos{
    display: grid;
    width: 60vw;
    align-items: center;
    justify-items: center;
    grid-template-columns: repeat(2,50%);
    grid-template-rows: repeat(2,300px);
    grid-gap: 10px;
    box-shadow: #e6ebfa 0px 0px 20px 10px;
    border-radius: 15px;
    margin: auto;
    margin-bottom: 20px;
}

.cont-NAcademicos>div{
    width: 250px;
    height: 75%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 5px;
    margin-bottom: 5px;
    border-radius: 15px;
    /* background-color: #27374f; */
    color: #27374f;
    transition: box-shadow 0.3s;
}

.cont-NAcademicos>div:hover{
    box-shadow: #e6ebfa 0px 0px 20px 10px;
}

.cont-NAcademicos>div>span{
    font-weight: 600;
}

.cont-NAcademicos>div>span:nth-child(1){
    font-size: 19px;
    margin-bottom: 7px;
}

.cont-NAcademicos>div>span:nth-child(2){
    font-size: 13px;
    margin-bottom: 13px;
}

.cont-NAcademicos>div>span:nth-child(2)::before{
    content: "(";
}

.cont-NAcademicos>div>span:nth-child(2)::after{
    content: ")";
}

.cont-NAcademicos>div>p{
    padding: 0px 40px;
    text-align: justify;
    font-size: 13px;
}

.cont-NAcademicos>div:hover{
    cursor: pointer;
    transform: translate3d(0,-5px,0);
    transition: transform 0.5s;
}
.cont-Videos{
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.cont-Videos>h2{
    font-size: 25px;
    text-align: center;
}

.cont-Videos>div{
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: center;
}

.cont-Videos>div>video{
    width: 300px;
    height: 300px;
    margin: 20px;
    box-shadow: #27374f 0px 0px 4px 2px;
    border-radius: 20px;
}


@media (max-width: 1200px) {
    .cont-quienes>div:first-child{
        width: 70%;
        align-items: center;
    }

    .cont-quienes>div:first-child>h3{
        width: max-content;
    }

}

@media (max-width: 1000px){
    .cont-NAcademicos{
        width: 85vw;
    }
}

@media (max-width: 720px){
    .cont-quienes>div:last-child{
        grid-template-columns: repeat(2,235px);
        grid-template-rows: repeat(3,154px);
    }

    .cont-NAcademicos{
        width: 90vw;
        margin: 0%;
    }
    .cont-NAcademicos>div{
        width: 200px;
    }
    .cont-NAcademicos>div>p{
        text-align: center;
    }
    
    .cont-Videos>div>video{
        width: 250px;
        height: 250px;
    }
}


@media (max-width: 550px){
    .cont-quienes>div:last-child{
        grid-template-columns: repeat(2,157px);
        grid-template-rows: repeat(3,103px);
    }

    .cont-NAcademicos{
        width: 90%;
        justify-items: center;
        justify-content: center;
        grid-template-columns: 50%;
        grid-template-rows: repeat(4,300px);
    }

}

@media (max-width: 350px){
    .cont-NAcademicos{
        width: 95%;
    }
    .cont-NAcademicos>div{
        font-size: 10px;
    }
    .cont-Videos>div>video{
        width: 200px;
        height: 200px;
    }

    .cont-quienes>div:last-child{
        grid-template-columns: repeat(2,117px);
        grid-template-rows: repeat(3,77px);
        margin: 5%;
    }
}