
.cont-slider{
    width: 100%;
    position: relative;
}

.cont-slider>.slider{
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 727px;
}

.slider::before{
    content: "";
    position: absolute;
    top: 0%;
    left: 33%;
    width: 35%;
    height: 100%;
    background: linear-gradient(60deg,hsla(337, 85%, 31%, 0.85) 50%,transparent 50%);
    z-index: 20;
}

.slider::after{
    content: "";
    position: absolute;
    top: 0%;
    left: 0%;
    background: hsla(337, 85%, 31%, 0.85);
    width: 33%;
    height: 100%;
    z-index: 20;
}


.slider>img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 727px;
    object-fit: cover;
    transition: opacity 1s;
}

.cont-slider .cont-info{
    position: absolute;
    top: 50%;
    left: 20%;
    z-index: 100;
    color: white;
    display: flex;
    width: 80%;
    justify-content: space-between;
    transform: translate(-20%,-50%);
}

.cont-slider .cont-info>div{
    width: 40vw;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    font-size: 50px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.cont-slider .cont-info>div:last-child{
    align-items: flex-end;
}

.cont-slider .cont-info>div:last-child>img{
    width: 60%;
    height: 100%;
}

.cont-slider .cont-info>div>h2{
    margin: 0;
    text-shadow: black -1px -1px 10px;
}

.cont-slider .cont-info>div>p{
    font-size: 15px;
    text-align: justify;
    font-weight: 600;
    font-family: Arial, Helvetica, sans-serif;
    line-height: 1.4;
    margin-top: 50px;
    text-shadow: black -10px -1px 10px;
}

.imgActive{
    z-index: 10;
    opacity: 1;
}

.imgInactive{
    z-index: 0;
    opacity: 0;
}


@media (min-width:1600px) {
    .slider::before{
        width: 30%;
    }
}

@media (max-width:1250px) {
    .slider::before{
        width: 55%;
        background: linear-gradient(65deg,hsla(337, 85%, 31%, 0.85) 50%,transparent 50%);
    }
    .cont-info{
        width: 95% !important;
    }
}

@media (max-width:920px) {
    .cont-info h2{
        font-size: 45px;
    }
}

@media (max-width:820px) {
    .slider{
        height: 500px;
    }
    .slider::before{
        width: 55%;
        background: linear-gradient(70deg,hsla(337, 85%, 31%, 0.85) 50%,transparent 50%);
    }
    .cont-himno{
        padding-left: 50px !important;
        padding-right: 50px !important;
    }

}


@media (max-width:520px) {
    .cont-info h2{
        font-size: 35px;
    }
    .slider::before{
        background: linear-gradient(75deg,hsla(337, 85%, 31%, 0.85) 50%,transparent 50%);
    }
    .cont-himno{
        padding-left: 20px !important;
        padding-right: 20px !important;
    }
    .cont-himno>p{
        font-size: 13px;
    }
}

@media (max-width:360px) {
    .slider::before{
        left: 33%;
        width: 60%;
        height: 100%;
        background: linear-gradient(75deg,hsla(337, 85%, 31%, 0.85) 50%,transparent 50%);
    }
    .cont-info>div:last-child{
        display: none;
    }
    .cont-info>div:first-child>h2{
        font-size: 25px;
    }
    .cont-himno{
        padding-left: 10px !important;
        padding-right: 10px !important;
    }
    .cont-himno>p{
        font-size: 11px;
        font-style: normal !important;
    }
}