.cont-admiciones{
    width: 100vw;
    margin-top: 100px;
    color: #27374f;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 50px;
}

.cont-req-admi{
    width: 90%;
    margin-bottom: 50px;
}

.cont-req-admi>h2{
    font-size: 50px;
    font-weight: 100;
    margin: 0;
    margin-left: 50px;
    line-height: 1.5;
    text-align: center;
}

.cont-req-admi>h2:first-child{
    margin-top: 70px;
}

.cont-req-admi>h2:nth-child(2){
    margin-bottom: 70px;
}

.cont-req-admi>h2>span{
    color: #900C3F;
}

.cont-tarje-Admi{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.tarjeta-admi{
    width: 400px;
    height: 450px;
    box-shadow: grey 0px 0px 10px 3px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin: 20px;
}

.tarjeta-admi>h2,
.tarjeta-admi>p{
    width: 300px;
    margin: 0%;
    line-height: 1.5;
}

.tarjeta-admi>h2{
    margin-top: 50px;
    margin-bottom: 20px;
    font-weight: 100;
    font-size: 30px;
}

.tarjeta-admi>p{
    font-size: 15px;
}

.cont-img-Pago{
    width: 60%;
}

.cont-img-Pago>img{
    width: 100%;
}

.cont-img-Pago>div{
    display: flex;
    align-items: center;
    margin: 50px 0px;
}

.cont-img-Pago:last-child{
    margin-bottom: 100px;
}

.cont-img-Pago>div>img{
    width: 30%;
    height: auto;
}



.cont-img-Pago>div>h2{
    font-size: 45px;
    text-align: center;
}


.cont-img-Pago>div>h2>span{
    color: #900C3F;
}

.cont-img-Pago>div>p,
.cont-img-Pago>div>div>p{
    font-weight: 600;
    position: relative;
    font-size: 31px;
    margin-left: 50px;
}

.cont-img-Pago>div>div>p{
    font-size: 28px;
}

.cont-img-Pago>div>p>span,
.cont-img-Pago>div>div>p>span{
    position: absolute;
    left: -20px;
}

.cont-recordar{
    width: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.cont-recordar h2{
    font-size: 40px;
}

.cont-recordar p{
    width: 80%;
    font-size: 22px;
    text-align: left;
    font-weight: 600;
}

@media (max-width:820px) {
    .cont-img-Pago p,
    .cont-img-Pago h2{
        font-size: 20px !important;
    }
    .cont-req-admi>h2{
        font-size: 40px;
    }
}
@media (max-width:520px) {
    .cont-img-Pago p,
    .cont-img-Pago h2{
        font-size: 15px !important;
    }
    .cont-req-admi>h2{
        font-size: 30px;
    }
    .cont-recordar>h2{
        font-size: 30px;
    }
    .cont-recordar>p{
        font-size: 15px;
    }
}
@media (max-width:380px) {
    .cont-img-Pago p,
    .cont-img-Pago h2{
        font-size: 12px !important;
    }
    .tarjeta-admi>h2{
        font-size: 25px;
    }

    .tarjeta-admi>p{
        font-size: 12px;
    }

    .tarjeta-admi>h2,
    .tarjeta-admi>p{
        text-align: center;
    }

    .cont-recordar>h2{
        font-size: 25px;
    }
    .cont-recordar>p{
        font-size: 12px;
    }
}
